import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NewRideComponent } from './view/new-ride.component';

const routes: Routes = [{ path: '', component: NewRideComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class NewRideRoutingModule {}
