import { AfterViewInit, Component, ViewChild } from '@angular/core';
import { AlertService } from 'src/app/shared/services/alert-service/alert-service.service';
import { MapLoaderService } from 'src/app/shared/services/map-loader/map-loader.service';

@Component({
  selector: 'app-new-ride',
  templateUrl: './new-ride.component.html',
  styleUrls: ['./new-ride.component.scss'],
})
export class NewRideComponent implements AfterViewInit {
  @ViewChild('gmap') gmapElement: any;
  defaultLocation = { lat: 24.886, lng: -70.268 };

  constructor(public mapLoader: MapLoaderService, private alertService: AlertService) {}

  ngAfterViewInit(): void {
    this.initMap();
  }
  initMap() {
    this.mapLoader.initMap(this.gmapElement, this.defaultLocation.lat, this.defaultLocation.lng).then(() => {});
  }
}
