/* eslint-disable no-undef */
import { Component, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, FormGroupDirective } from '@angular/forms';
import { Validators } from 'angular-reactive-validation';
import { Subject } from 'rxjs';
import { slideInOutAnimation } from 'src/app/shared/animations/animations';
import { IFormErrors } from 'src/app/shared/directives/forms-directives/validation-error-directive/error-message.interface';
import { AlertService } from 'src/app/shared/services/alert-service/alert-service.service';
import { SessionService } from 'src/app/shared/services/session-service/session.service';

@Component({
  selector: 'app-new-ride-form',
  templateUrl: './new-ride-form.component.html',
  styleUrls: ['./new-ride-form.component.scss'],
  animations: slideInOutAnimation,
  encapsulation: ViewEncapsulation.None,
})
export class NewRideFormComponent implements OnInit, OnDestroy {
  @ViewChild(FormGroupDirective) myForm: any;
  public today = new Date();
  public isLoading = false;
  addCustomerDisplay = false;
  errorMessages: IFormErrors[] = [];
  ngNewRideForm!: FormGroup;
  submitted = false;
  customerList: {
    id: number;
    name: string;
  }[] = [];
  customerName = '';

  rideTypesList: { id: number; name: string; price: string; checked: false }[] = [
    {
      id: 0,
      name: 'Regular',
      price: '$10',
      checked: false,
    },
    {
      id: 1,
      name: 'Regular',
      price: '$10',
      checked: false,
    },
    {
      id: 2,
      name: 'Regular',
      price: '$10',
      checked: false,
    },
    {
      id: 3,
      name: 'Regular',
      price: '$10',
      checked: false,
    },
    {
      id: 4,
      name: 'Regular',
      price: '$10',
      checked: false,
    },
    {
      id: 5,
      name: 'Regular',
      price: '$10',
      checked: false,
    },
  ];

  destroyed = new Subject<boolean>();
  is12HTimeZone = false;
  pickupAddressList = [];
  dropOffAddressList = [];
  constructor(private fb: FormBuilder, private alertService: AlertService, private sessionService: SessionService) {
    this.is12HTimeZone = this.checkTimeFormat(this.sessionService.locale);
  }

  public get elements() {
    return this.ngNewRideForm.controls;
  }

  checkTimeFormat(language: string) {
    const options = new Intl.DateTimeFormat(language, {
      hour: 'numeric',
    }).resolvedOptions();
    return options.hour12 ?? false;
  }

  ngOnInit(): void {
    this.createForm();
  }

  createForm() {
    this.ngNewRideForm = this.fb.group({
      customerId: [null, Validators.required('[[[[Customer is required]]]]')],
      pickupAddress: [null, Validators.required('[[[[Pickup address is required]]]]')],
      pickupTime: [null],
      dropoffAddress: [null, Validators.required('[[[[Dropoff address is required]]]]')],
      rideType: [null, Validators.required('[[[[Ride type is required]]]]')],
      remarks: [''],
      language: [''],
      nationality: [''],
      babySeat: [''],
      wheelChairAccess: [''],
    });
  }

  setCarPreference(event) {
    console.log(event);
  }

  resetForm() {
    this.myForm.resetForm();
  }

  ngOnDestroy() {
    this.destroyed.next(true);
  }

  filterPickupAddress(event: any) {
    console.log(event);
  }

  filterDropOffAddress(event: any) {
    console.log(event);
  }

  filterCustomers(event: any) {
    const query = event.query;
    this.customerList = this.customerList.filter((customer) => {
      return customer.name.toLowerCase().includes(query.toLowerCase());
    });
    console.log(query);
  }

  noDropOffChanged(event) {
    console.log(event);
  }

  rideTypesChanges(event) {
    console.log(event);
  }

  submitNewRide(formGroup: FormGroup) {
    this.submitted = true;
    console.log(formGroup.value);
    if (formGroup.valid) {
      this.isLoading = true;
    }
  }
}
